
@import '../../assets/css/common.css';
@import '../../assets/css/date-picker-bottom.css';
.doctor-form{
    padding: 10px 15px 60px 15px;
}
.steps-bar{
    position: relative;
    margin-top: 10px;
    padding: 10px 15px;
}
.steps-wrapper{
    border-top: 2px solid #ccc;
}
.steps-flex{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 0px 30px;
}
.steps-item{
    min-width: 30px;
    text-align: center;
    color: #ccc;
    font-size: 1.4rem;
}
.steps-item-cur{
    color: #49ced4;
    font-size: 1.6rem;
}
.steps-dot{
    width: 6px;
    height: 6px;
    background: #ccc;
    border-radius: 50%;
    margin: -4px auto 5px auto;
}
.steps-item-cur .steps-dot{
    width: 8px;
    height: 8px;
    background: #49ced4;
    margin: -5px auto 5px auto;
}



